[mol_chart] {
	display: flex;
	flex-direction: column;
	flex: 1 1 50vmin;
}

[mol_chart_titler] {
	display: flex;
	justify-content: center;
	font-weight: bold;
	padding: .5em;
}

[mol_chart_plotter] {
	flex: 1 1 auto;
	height: 25vmin;
}

[mol_chart_legender] {
	padding: .5em;
	border-spacing: .5em;
	margin-left: 3em;
}

[mol_chart_legender_info] {
	display: table-row;
}

[mol_chart_legender_info] > * {
	display: table-cell;
}

[mol_chart_legender_info_titler] {
	flex: 1 1 auto;
}
