[mol_plotter] {
	position: relative;
	display: flex;
	margin: 0 0 1.5em 4em;
}

[mol_plotter_grid] {
	position: absolute;
	left: 0;
	width: 100%;
	display: flex;
}

[mol_plotter_grid_labeler] {
	flex: 0 1 3em;
	text-align: right;
	margin: -.5em -.5em;
	position: absolute;
	transform: translate(-100%,0);
}

[mol_plotter_grid_liner] {
	border-top: 1px solid #CBCACB;
	flex: 1 1 auto;
}

[mol_plotter_svg] {
	flex: 1 1 auto;
	position: relative;
}


[mol_plotter_grid_vert] {
	position: absolute;
	top: 0;
	height: 100%;
	display: flex;
}

[mol_plotter_grid_vert_labeler] {
	flex: 0 1 3em;
	text-align: center;
	position: absolute;
	bottom: 0;
	transform: translate(-50%,150%);
}

[mol_plotter_grid_vert_liner] {
	border-left: 1px solid #CBCACB;
	flex: 1 1 auto;
}
